.landing{
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content:space-between;
}

.landing .top{
    width: 95%;
    padding:10px 30px;
    display:flex;
    flex-flow:row;
    justify-content:space-between;
    align-items:center;
    background: rgba(0,0,0,0.1);
    margin: 30px 0;
    border-radius: 5px;
    border: 2px solid rgba(0,0,0,0.1);
}  

.landing .top h3{   
    font-size: 30px;
    color: #444;
    font-weight: 500;
}


.landing .bottom{
    width: 95%;
    background: rgba(0,0,0,0.1);
    margin: 30px 0;
    padding: 20px 7px;
    text-align: center;
    border-radius: 5px;
    color: #444;
    border: 2px solid rgba(0,0,0,0.1);

}

.landing .bottom span{
    color: black;
    font-weight: bold;
}


.landing .middle {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items:center;
    margin: 30px 0;
    /* justify-content: center; */
}


.landing .middle form{
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
}

.landing .middle div{
    width: 500px;
    padding: 10px;
    margin: 15px 0;
    display: flex;
    flex-direction: column;
}

.landing .middle .profiles > select{
    height: 45px;
    font-size: 16px;
    color: #555;
    /* border-radius: 2px; */
    background: rgba(0,0,0,0.1);
    border: 2px solid rgba(0,0,0,0.1);
    outline: none;
    padding: 0 10px;
}

.landing .middle .profiles > select > option{
    background-color:rgba(0,0,0,0.1);
}

.landing .middle .pass_key > input{
    height: 45px;
    font-size: 16px;
    color: #555;
    /* border-radius: 2px; */
    background: rgba(0,0,0,0.1);
    border: 2px solid rgba(0,0,0,0.1);
    outline: none;
    padding: 0 10px;
}

.landing .middle .error{
    position: relative;
}
.landing .middle .error>input{
    border: 2px solid red;
}

.landing .middle  .error:after{
    content:"\f071";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    position: absolute;
    right: 20px;
    top: 20px;
    color: rgb(255, 0, 0);
}

.landing .middle .error .errors{
    position: absolute;
    top:0;
    transform: translateY(-50%);
}


.landing .middle .actions {
    align-items: center;    
}

.landing .middle .actions button{
    width: 200px;
    height: 45px;
    font-size: 16px;
    color: #555;
    /* border-radius: 2px; */
    background: rgba(0,0,0,0.1);
    border: 2px solid rgba(0,0,0,0.1);
    outline: none;
    padding: 0 10px;
    cursor:pointer;
    transition: all 0.3s ease-in-out;
}

.landing .middle .actions button:hover{
    background: none;
}


@media screen and (max-width:550px){
    .landing .middle form div{
        width: 95%;
        font-weight: 500;
    }
}