.progress_container{
    width:100%;
    padding:5px;
    border-radius: 10px;
    border: 2px solid #eee;
    /* height: 15px; */
}

.progress{
    height: 10px;
    width: 100%;
    background: #aaa;
    display:flex;
    justify-content:flex-start;
    border-radius: 10px;
}

.progress_bar{
    transition:all 0.6s ease-in-out;
    height: 100%;
    width: 0%;
    border-radius:5px;
    background: white;
    animation: load 0.6s linear ;
}

