.nav{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    margin:30px 0 0 0;
}

.nav .left{
    width: 10%;
    padding: 10px 20px;
    display:flex;
}

.nav .left img{
    border-radius: 50%;
    cursor: pointer;
}

.nav .right{
    width: 90%;
    padding: 0px 20px;
    display:flex;
    align-items: center;
    justify-content:flex-end;
}


.nav .right  ul{
    list-style: none;
    background: rgba(0,0,0,0.1);
    border-radius: 50px;
    padding: 5px;
}

.nav .right ul li{
    float: left;
    padding: 10px 20px;
}

.nav .right .nav_action{
    border-radius: 50px;
}

.nav .right .nav_action_active{
    background: #fff;
}

.nav .right .nav_action a{
    text-decoration: none;
    color: darkslategray;
    /* text-transform: uppercase; */
    font-size: 15px;
    font-weight: bold;
}

.nav .right .nav_action a:hover{
    color: #777;
}

@media only screen and (max-width: 400px) {
    .nav .right{
        padding: 0;
    }
}

@media (max-width: 768px){
    .nav .left, .nav .right{
        width: 100%;
        align-items: center;
        justify-content: center;
    }

    .nav .right{
        padding: 0;
    }

    .nav .right ul li{
        float: left;
        padding: 10px 15px;
    }

}