  }
  
*::-webkit-scrollbar-track {
    background: darkgray;        /* color of the tracking area */
  }
  
*::-webkit-scrollbar-thumb {
    background-color: white;    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    /* border: 3px solid orange;*/
  }

.projects_container{
    background: rgba(0,0,0,0.1);
    border-radius: 20px;
    padding: 20px;
    margin: 30px 0;
}

.projects_container .empty{
    display: flex;
    flex-flow: row wrap;
}

.projects_container .empty .left{
    width: 50%;
}

.projects_container .empty .left img{
    width: 100%;
    /* height: 100%; */
    border-radius: 20px;
}

.projects_container .empty .right{
    width: 50%;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projects_container .empty .right h3{
    text-align: center;
    font-size: 36px;
    color: #444;
}

.projects_container .empty .right h3 i{
    margin:20px 0;
}


/* projects  */
.projects_container .projects {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    /* align-items: center; */
    justify-content: space-between;
}

.projects_container .projects .project_item{ 
    width: 30%;
    background: rgba(0,0,0,0.1);
    padding: 10px;
    border-radius: 10px;
    margin:20px 0;
}

.projects_container .projects .project_item .top{ 
    position: relative;
    width: 100%;
    /* height: 300px; */
    margin: 0 0 20px 0;
    padding: 0;
    transition: all 0.3s ease-in-out;
    background-size: cover;
    border-radius:10px;
    overflow: auto;
}
.projects_container .projects .project_item img{
    width: 100%;
    border-radius: 10px;
}

.projects_container .projects .project_item .description{
    /* position: absolute; */
    width: 100%;
    max-height: 100px;
    overflow-y:auto;
    margin: 10px 0 15px 0;
    padding:0 10px;
    /* top: 0; */
    /* left: 0; */
    /* display: flex; */
    /* align-items: center; */
    /* justify-content:center; */
    /* background:rgba(0,0,0,0.6); */
    border-radius: 10px;
    color: #444;
    /* opacity: 0; */
    /* transform: rotateY(180deg); */
    transition: all 0.1s ease-in-out;
    /* overflow:hidden; */
}

/* .projects_container .projects .project_item .description p{ */
    /* margin: 10px 0; */
    /* height: 100%; */
    /* overflow:auto; */
    /* padding:0 10px; */
/* } */

.projects_container .projects .project_item .top:hover{
    transform: scale(0.95);
}

.projects_container .projects .project_item .heading{
    font-size: 24px;
    margin: 0 0 20px 0;
    padding: 0 10px;
    /* text-align: center; */
}
.projects_container .projects .project_item .links{
    padding: 0 10px;
    margin: 0 0 10px 0;
    font-size: 20px;
}

.projects_container .projects .project_item .links a{
    text-decoration: none;
    text-align: center;
    margin: 0 10px 0 0;
    color: #444;
    width: 45px;
    line-height: 45px;
    display: inline-block;
    background:#ddd;
    border-radius: 35px;
    transition:all 0.4s ease-in-out;
}

.projects_container .projects .project_item .links a:hover{
    color: white;
}

.projects .icon_GH:hover {
    background: #161b22 !important;
}

.projects .icon_OT:hover {
    background: #0a66c2 !important;
}

.projects_container .actions{
    width: 100%;
    padding: 10px;
}

.projects_container .actions button{
    padding: 10px 30px;
    font-size: 14px;
    font-weight: bold;
    border: white solid 2px;
    border-radius: 20px;
    color: #333;
    background: white;
    transition: all 0.4s ease-in-out;
    cursor:pointer;
}

.projects_container .actions button:hover {
    background: none;
}




@media screen and (max-width:1000px){
    .projects_container .projects .project_item{ 
        width: 47%;
    }
}

@media screen and (max-width:700px){
    .projects_container .empty{
        flex-direction: column-reverse;
    }

    .projects_container .empty .left {
        width:100%;
    }
 
    .projects_container .empty .right h3{
        line-height: 45px;
    }

    .projects_container .empty .right{
        width:100%;
        margin: 30px 0;
    }

    .projects_container .projects{
        justify-content:space-around;
    }
    .projects_container .projects .project_item{ 
        width: 95%;
    }

    .projects_container .projects .project_item .top{
        height: auto;
    } 

}