button[disabled]{
    background:  #ccc !important;
    border-color:#ccc !important;
    cursor: not-allowed !important;
    /* pointer-events: none !important; */
}
.contact{
    background: rgba(0,0,0,0.1);
    padding:20px;
    border-radius:20px;
    display:flex;
    flex-flow: row wrap;
    align-items: center;
    margin: 30px 0;
}

.contact .left{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
}

.contact form{
    width: 90%;
}

.contact form div{
    display: flex;
    flex-direction: column;
    margin: 15px 0;
    transition: all 0.3s ease-in-out;
}

.contact form div > input , textarea{
    line-height:45px;
    font-size: 16px;
    background: rgba(0,0,0,0.08);
    border: 2px solid rgba(0,0,0,0.08);
    outline: none;
    padding: 0 20px;
    /* font-weight: 500; */
    border-radius: 20px;
    color: #333;
}

.contact form div label, .contact form div .errors{
    margin: 0 0 5px 10px;
}

.contact form .actions{
    flex-direction: row;
    justify-content: space-around;
    margin: 45px 0 30px 0;
}

.contact form .actions button{
    width: 40%;
    padding: 15px;
    font-size: 15px;
    font-weight: bold;
    background: white;
    color: darkslategray;
    border-radius: 25px;
    border:2px solid white;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.contact form .actions button:hover{
    background: rgba(0,0,0,0.08);
}


.contact .right{
    width: 50%;
}


.contact .right img{
    /* max-height: 600px;    */
    width: 100%;
    border-radius: 20px;
}


/* form controls      */

.contact form .error > input, form .error >textarea{
    border-color: rgb(255, 0, 0) !important;
}

.contact form .success > input, form .success > textarea{
    border-color: rgb(9, 91, 9 ,63%) !important;
}

.contact form > .success , .error{
    position: relative;
}

.contact form .error:after{
    content:"\f071";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    position: absolute;
    right: 15px;
    top: 40px;
    color: rgb(255, 0, 0);
}

.contact form .success:after{
    content:"\f058";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    position: absolute;
    right: 15px;
    top: 40px;
    color: rgb(9, 91, 9,63%) ;
}

/* -------------------- */


@media screen and (max-width:900px){
    .contact .right{
        width: 100%;
    }

    .contact .left{
        width: 100%;
    }
}