.about{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    background: rgba(0,0,0,0.1);
    padding: 20px;
    border-radius: 20px;
    align-items: center;
    margin: 30px 0;
}

.about .left{
    width: 50%;
    margin: 20px 0 0 0;
    padding:0 10px;
}

.about .right{
    width: 50%;
    padding: 0 0 0 50px;
    display: flex;
    flex-direction: column;
    justify-content:center;
}


.about .left h4 {
    font-size: 36px;
    text-transform: uppercase;
    margin: 0 0 20px 0;
}

.about .left p{
    margin: 0 0 40px 0;
    color: var(--faint-text-color);
}

.about .right .skill_bar{
    width:80%;
    margin: 10px 0;
}

.about .right .skill_bar h4{
    font-weight: 500;
    margin: 0 0 10px 5px;
}

@media screen and (max-width:600px){
    .about .left h4{
        line-height:55px;
    }
}

@media screen and (max-width:900px){
    .about .left,.about .right{
        width: 100%;
    }

    .about .right{
        padding:0;
    }
    .about .right .skill_bar{
        width: 95%;
    }
}