.home{
    width: 100%;
    overflow-x: hidden;
    margin:30px 0 30px 0;
    padding: 20px;
    /* background: rgba(255,255,255,0.8); */
    background: rgba(0,0,0,0.1);
    border-radius: 15px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
}

.home .left{
    width:40%;
    display: flex;
    align-items: center;
}

.home .left img{
    width: 100%;
    border-radius: 15px;
}

.home .right{
    width: 60%;
    height: 100%;
    color: #222;
    padding: 0 0 0 50px;
    display:flex;
    flex-direction: column;
}

.home .personal_info .greeting{
    text-transform: uppercase;
    font-size: 14px;
    font-weight: normal;
    letter-spacing:2.1px;
    margin: 0 0 12px 0;
}

.home .personal_info .name{
    text-transform: uppercase;
    font-size: 48px;
    margin: 0 0 15px 0;
}

.home .personal_info .tagline{
    text-transform:uppercase;
    font-size:16px;
    font-weight: 500;
    margin-bottom: 20px;
}

.home .personal_info .bio{
    color: #555;
    margin-bottom: 40px;
}

.home .right .list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.home .personal_info .basic_info  li{
    margin: 0 0 15px 0;
}

a{
    text-decoration: none;
    transition: all 0.2s ease-in-out;
}

.home .personal_info .basic_info li a{
    position: relative;
    font-size: 16px;
    padding: 0 0 0 40px;
    color: var(--faint-text-color);
}

.home .personal_info .basic_info li a i{
    position: absolute;
    left: 0;
    top: 50%;
    transform:translateY(-50%);
    font-size: 20px;
    /* color: #766dff; */
    color: #333;
}


.home .personal_info .social_links{
    margin:45px 0 0 0;
}

.home .personal_info .social_links li{
    display: inline-block;
    margin:7px 7px 0 0;
}

.home .personal_info .social_links li a{
    display: inline-block;
    line-height: 45px;
    width: 45px;
    font-size: 16px;
    background: #fff;
    text-align: center;
    border-radius: 50%;
    color: var(--faint-text-color);
}
.home .personal_info .social_links li a:hover{
    color: white;
}

.icon_FB:hover > a{
    background: blue!important;
}

.icon_LI:hover > a {
    background: #0a66c2 !important;
}

.icon_IG:hover > a {
    background: #da1086 !important;
}

.icon_TW:hover > a {
    background: #1da1f2 !important;
}

.icon_GH:hover > a {
    background: #161b22 !important;
}

.icon_YT:hover > a {
    background: #fb4443 !important;
}

.icon_TR:hover > a {
    background: #00152c !important;
}

.icon_RD:hover > a {
    background: #fb4b43 !important;
}

.icon_SK:hover > a {
    background: #0178ca !important;
}

.icon_TG:hover > a {
    background: #26a7e8 !important;
}

.icon_OT:hover > a {
    background: #0a66c2 !important;
}


  /* 
  Extra small devices (phones, 600px and down) */
  @media only screen and (max-width: 600px) 
  {
    .home .right , .home .left{
        width:100%;
    }

    .home .right{
        padding: 50px 0 0 0;
    }

    .home .personal_info h3{
        line-height: 55px;
    } 
  } 

/* Small devices (portrait tablets and large phones, 600px and up) 
  */
@media only screen and (min-width: 600px) {
    .home .right , .home .left{
        width:100%;
    }

    .home .right{
        padding: 50px 0 0 0;
    }
} 

/* Medium devices (landscape tablets, 768px and up) */

  @media only screen and (min-width: 768px) {
    .home .right , .home .left{
        width:100%;
    }
  } 

/* Large devices (laptops/desktops, 992px and up) 
  */

  @media only screen and (min-width: 992px) {
    .home .left, .home .right {
        width:50%;
    }
    .home .right{
        padding: 0px 0 0 50px;
    }
  } 

/* Extra large devices (large 
  laptops and desktops, 
  1200px and up) */
@media only screen and (min-width: 1200px) {
    .home .left {
        width:40%;
    }

    .home .right {
        width:60%;
    }
} 